*
{
  margin: 0;
  padding: 0;
}
body
{

  margin: 0;
  padding: 0;
  //text-transform: uppercase;
}
ul
{
  margin: 0;
  padding: 0;
  list-style: none;
}
a
{
  text-decoration: none;
}
h1,
h2,
h3,
h4,
h5
{
  margin: 0;
}
*
{
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
.container{
  margin: 0 auto;
  max-width: 1200px;
  @media screen and (max-width: 993px){
    margin: 0 auto;
    max-width: 95%;
    width: 100%;
  }
  @media screen and (max-width: 577px){
    margin: 0 auto;
    max-width: 95%;
    width: 100%;
  }
}
