.not_found{
  background: url("../img/404_bg.png") no-repeat;
  background-size: cover;
  height: 100vh;
}
.not_found_page_wrapper{
  display: flex;
  flex-direction: column;
  align-items: center;
}
.not_found_page_img{
  display: flex;
}
.not_found_page_img img{
  margin: 0 auto;
}
.not_found_title{
  font-family: 'Akzidenz-Grotesk Pro Med Ext';
  font-style: normal;
  font-weight: 500;
  font-size: 48px;
  line-height: 140%;
  /* or 67px */

  text-align: center;
  text-transform: uppercase;

  /* синий */

  color: #002C42;
  margin-bottom: 87px;
}
.not_found_text{
  font-family: Inter;
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 140%;
  text-align: center;
  color: #232323;
  margin-bottom: 20px;
}
.back_to_main{
  font-family: Inter;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 140%;
  color: #232323;

  background: #FFA800;
  border-radius: 3px;
  //width: 270px;
  //height: 50px;
  padding: 14px 90px;
}

@media screen and (max-width: 577px){
  .not_found_title{
    font-size: 30px;
    margin-bottom: 10px;
  }
  .not_found_page_img{
    width: 90%;
  }
  .not_found_page_img img{
    width: 100%;
  }
}
