body {
  position: relative;
}

.site_bar_wrapper {
  padding-top: 20px;
  position: fixed;
  top: 0;
  left: 50%;
  width: 100%;
  transform: translateX(-50%);
  background: linear-gradient(180deg, #FFFFFF 0%, rgba(255, 255, 255, 0) 100%);
  min-height: 80px;
  z-index: 2;
}

.site_bar_wrapper_scrolled {
  height: 90px;
  background: #fff;
  width: 100%;
}

.site_bar_inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav_list {
  display: flex;
  gap: 40px;
}

.nav_list_item:first-child {
  position: relative;
  max-height: 50px;
  overflow: hidden;
  padding-right: 20px;
}

.nav_list_item:first-child:after {
  content: url("../img/drop_arrow_icon.svg");
  position: absolute;
  top: -2px;
  right: 0px;
  cursor: pointer;
}

.nav_list_item:last-child {
  position: relative;
  max-height: 50px;
  overflow: hidden;
  padding-right: 20px;
}

.nav_list_item:last-child:after {
  content: url("../img/drop_arrow_icon.svg");
  position: absolute;
  top: -3px;
  right: 0px;
  cursor: pointer;
}

.nav_list_item {
  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  color: #232323;
  //min-height: 50px;
}

.nav_list_item a {
  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  color: #232323;
}

.call_btn {
  position: relative;
  overflow: hidden;
  min-height: 50px;
  display: flex;
}

.call_btn:hover {
  background-color: #FF6E1C;
}

.call_btn span:before {
  content: "";
  position: absolute;
  top: -50%;
  right: -50%;
  bottom: -50%;
  left: -50%;
  background: linear-gradient(to bottom, rgba(229, 172, 142, 0), rgba(255, 255, 255, .7) 50%, rgba(229, 172, 142, 0));
  //transform: rotateZ(60deg) translate(-5em, 7.5em);
  //animation: sheens 3s infinite;
  z-index: 100;
  //animation: .5s linear 1s infinite alternate slidein;
  animation-name: anim_top_btn;
  animation-duration: 2.5s;
  animation-iteration-count: infinite;
}

@keyframes anim_top_btn {
  0% {
    left: -500px;
    //transform: translateX(0);
    transform: rotateZ(60deg);
  }
  100% {
    left: 300px;
    //transform: translateX(300px);
    transform: rotateZ(60deg);

  }
}

.call_btn span {
  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 140%;
  color: #232323;

  background: #FFA800;
  border-radius: 3px;
  padding: 14px 20px;
}

