  font-family: Inter;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 140%;
  /* or 22px */
  text-align: right;
  color: #002C42;
}
.cat_slide_price {
  display: flex;
  justify-content: space-around;
  background: rgba(149, 188, 208, 0.1);
  padding: 13px 0;
}
.cat_price_title {
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 140%;
  text-align: center;
  color: #232323;
}
.cat_price_text {
  font-family: Inter;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 140%;
  text-align: center;
  color: #002C42;
  margin-top: 7px;
}
.cat_rend_btn {
  font-family: Inter;
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 140%;
  color: #232323;
  width: 100%;
  text-align: center;
  background: #FFA800;
  border-radius: 3px;
  padding: 13px 0;
  cursor: pointer;
}
.catalog_sliders_col_list_btn {
  font-family: Inter;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 140%;
  color: #232323;
  border: 1px solid #FFA800;
  width: 410px;
  margin-top: 50px;
  padding: 14px 63px;
  display: block;
}
.cat_sliders_popup_wrapper {
  display: none;
}
.cat_sliders_popup_wrapper_active {
  min-width: 885px;
  margin: 0 auto;
  display: flex;
  position: fixed;
  background: rgba(35, 35, 35, 0.5);
  width: 100%;
  z-index: 10;
  align-items: center;
  top: 0;
  left: 0;
  height: 100vh;
}
.cat_sliders_popup_sub_wrapper {
  padding: 55px 69px;
  display: flex;
  flex-direction: column;
  max-width: 885px;
  margin: 0 auto;
  background: #fff;
}
.cat_close_icon {
  width: 24px;
  height: 24px;
  cursor: pointer;
  margin-left: auto;
}
.cat_popup_top_block {
  padding-bottom: 12px;
  border-bottom: 1px solid rgba(196, 196, 196, 0.5);
}
.cat_popup_top_title {
  font-family: 'Akzidenz-Grotesk Pro Med Ext';
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 140%;
  color: #424F60;
}
.cat_popup_top_text {
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 140%;
  /* identical to box height, or 20px */
  margin-top: 10px;
  color: #000000;
}
.cat_popup_bottom_block {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  margin-top: 19px;
}
.cat_popup_left_block {
  width: 49%;
}
.cat_popup_inner {
  width: 100%;
  display: flex;
  background: #F9F9F9;
  align-items: center;
  justify-content: center;
}
.cat_popup_inner img {
  padding: 8px 0;
}
.cat_popup_descr_inner {
  margin-top: 22px;
}
.cat_popup_descr_title {
  font-family: Inter;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 140%;
  color: #424F60;
}
.cat_form_right_block {
  width: 49%;
}
.cat_form_inner {
  display: flex;
  flex-direction: column;
  gap: 45px;
}
.input_sub_inner {
  display: flex;
  flex-direction: column;
}
.input_sub_inner lable {
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 140%;
  color: #232323;
}
.input_sub_inner input {
  width: 100%;
  height: 50px;
  margin-top: 10px;
  border: 1px solid #E1E1E1;
  outline: none;
  padding-left: 20px;
}
.input_sub_inner input[type="text"] {
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 140%;
  color: #C6CACF;
}
.cat_form_btn {
  font-family: Inter;
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 140%;
  color: #232323;
  width: 100%;
  text-align: center;
  padding: 13px 0;
  border: none;
  background: #FFA800;
  border-radius: 3px;
  cursor: pointer;
}
.catalog_nav {
  display: none;
}
.catalog_sliders_col_item:hover .catalog_nav {
  display: flex;
  width: 100%;
  position: absolute;
  top: 0;
  z-index: 1;
  justify-content: space-between;
  height: 99%;
  background: rgba(0, 44, 66, 0.4);
}
.catalog_sliders_btn_prev,
.catalog_sliders_btn_next {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  position: relative;
  z-index: 2;
  background: #FFA800;
  top: 50%;
  margin: 0 20px;
}
.catalog_sliders_btn_next {
  transform: rotate(180deg);
}
.catalog_sliders_col_item .swiper-pagination-catalog {
  position: absolute;
  left: 40%;
  bottom: 22px;
  z-index: 10;
}
.catalog_sliders_col_item .swiper-pagination-bullet {
  width: 8px;
  height: 8px;
  background: #fff;
  cursor: pointer;
  opacity: 1;
  margin: 0 8px !important;
}
.catalog_sliders_col_item .swiper-pagination-bullet-active {
  width: 8px;
  height: 8px;
  background: #FFA800;
  opacity: 1;
}
@media screen and (max-width: 577px) {
  .catalog_description_title {
    font-size: 28px;
  }
  .catalog_description_inner {
    flex-direction: column;
  }
  .cat_descr_img {
    width: 100%;
  }
  .cat_descr_text {
    width: 100%;
    margin-top: 30px;
  }
  .cat_min_inner {
    width: 48%;
    border: 1px solid #c4c4c4;
    margin-top: 15px;
    min-height: 64px;
    padding: 12px 6px;
  }
  .cat_descr_adv_inner {
    flex-wrap: wrap;
    margin-top: 35px;
  }
  .cat_min_text {
    margin-left: 10px;
    font-size: 12px;
  }
  .catalog_sliders_wrapper {
    margin-top: 100px;
  }
  .catalog_sliders_title {
    font-size: 28px;
    margin-bottom: 0;
  }
  .catalog_sliders_col_list {
    flex-direction: column;
  }
  .catalog_sliders_col_item {
    width: 100%;
  }
  .catalog_sliders_description {
    width: 100%;
    margin-top: 20px;
  }
  .catalog_sliders_main_title {
    font-size: 18px;
  }
  .cat_sliders_spec_inner {
    margin-top: 20px;
  }
  .cat_slide_price {
    margin-top: 40px;
  }
  .cat_rend_btn {
    margin-top: 20px;
  }
  .help_chose_wrapper {
    margin-top: 100px;
  }
  .doc_title_bold {
    margin-right: 7%;
  }
  .doc_link_wrapper {
    margin-top: 40px;
  }
  .doc_link_inner a {
    margin: 0 auto;
  }
  .contact_us_wrapper {
    margin-top: 100px;
  }
  .contact_main_title {
    font-size: 30px;
  }
  .cat_sliders_popup_wrapper_active {
    min-width: 100%;
  }
  .cat_sliders_popup_sub_wrapper {
    max-width: 95%;
    padding-top: 20px;
    padding-bottom: 50px;
    padding-left: 30px;
    padding-right: 30px;
  }
  .cat_popup_bottom_block {
    flex-direction: column;
  }
  .cat_popup_left_block {
    width: 100%;
  }
  .cat_form_right_block {
    width: 100%;
    margin-top: 30px;
  }
  .catalog_sliders_col_list_btn {
    width: 95%;
    text-align: center;
    padding: 14px 6px;
  }
  .cat_price_title {
    font-size: 12px;
  }
  .cat_price_text {
    font-size: 14px;
  }
}
